import { TlbWorld } from '../tlb'
import { Attack } from '../components/action'
import { Entity } from '../ecs/entity'
import { PositionComponent } from '../components/position'
import { Vector } from '../spatial'
import { calculateCover } from './cover'
import { getAim, getCriticalChance } from './stats'

export interface HitChanceCalculation {
  base: number
  distancePenalty: number
  coverPenalty: number
  final: number

  criticalHit: number
}

export function calculateHitChance(world: TlbWorld, entity: Entity, target: Entity, attack: Attack): HitChanceCalculation {
  const position = world.getComponent<PositionComponent>(entity, 'position')!
  const targetPosition = world.getComponent<PositionComponent>(target, 'position')!

  const cover = calculateCover(world, targetPosition.level, position.position, targetPosition.position)

  const distance = new Vector([position.position.fX - targetPosition.position.fX, position.position.fY - targetPosition.position.fY]).lN()
  const normalizedDistance = (10 * (distance - 1)) / attack.range

  const aim = getAim(world, entity) / 10
  const accuracy = attack.accuracy / 10
  const criticalHit = getCriticalChance(world, entity) / 10

  const base = Math.min(aim, accuracy)
  const distancePenalty = normalizedDistance < 2 ? 0 : normalizedDistance < 5 ? 0.1 : 0.2
  const coverPenalty = cover === 'full' ? 0.5 : cover === 'partial' ? 0.2 : 0
  return {
    base,
    distancePenalty,
    coverPenalty,
    final: base - distancePenalty - coverPenalty,

    criticalHit,
  }
}
