import { ComponentName, TlbSystem, TlbWorld, PushState } from '../tlb'
import { TakeTurnComponent, SelectionState } from '../components/rounds'
import { Queries } from '../renderer/queries'
import { WorldMapResource, WorldMap } from '../resources/world-map'
import { InputResource, Input } from '../resources/input'
import { Viewport, ViewportResource } from '../resources/viewport'
import { Entity } from '../ecs/entity'
import { Movement, Attack, Status, Action, SubAction, Jump } from '../components/action'
import { UIResource, UI } from '../resources/ui'
import { ScriptComponent } from '../components/script'
import { calculateAvailableActions } from '../component-reducers/available-actions'
import { SelectableAction } from '../ui/tabs/action-selector'
import { Random } from '../random'
import { attackTarget } from '../component-reducers/attack-target'
import { ItemComponent } from '../components/items'
import { consumeItem } from '../component-reducers/consume-item'
import { Distribution } from '../random/distributions'
import { applyEffect, clearExpiredEffects } from '../component-reducers/apply-effect'
import { createInventoryDescription } from '../component-reducers/inventory-description'
import { instantiateItem } from '../component-reducers/instantiate-item'
import { replacingEquip, unequip } from '../assets/characters'
import { Modal } from '../game-states/modal'
import { TriggeredByComponent } from '../components/trigger'

export class PlayerRoundControl implements TlbSystem {
  public readonly components: ComponentName[] = ['take-turn', 'player', 'position']
  private readonly random: Random

  public constructor(public readonly queries: Queries, public readonly pushState: PushState, public rng: Distribution) {
    this.random = new Random(rng)
  }

  public update(world: TlbWorld, entity: Entity): void {
    const script = world.getComponent<ScriptComponent>(entity, 'script')
    const takeTurn = world.getComponent<TakeTurnComponent>(entity, 'take-turn')!
    const isInAnimation = script !== undefined
    if (!isInAnimation) {
      const turnIsOver = takeTurn.acted && takeTurn.moved
      if (!turnIsOver) {
        const availableActions = calculateAvailableActions(world, entity, true)
        this.doTurn(world, entity, takeTurn, availableActions)
      } else {
        this.endTurn(world, entity)
      }
    }
  }

  public doTurn(world: TlbWorld, entity: Entity, takeTurn: TakeTurnComponent, availableActions: SelectableAction[]) {
    const ui: UI = world.getResource<UIResource>('ui')
    const input: Input = world.getResource<InputResource>('input')

    if (takeTurn.selectionState === undefined) {
      ui.showActionSelector(availableActions)
      takeTurn.selectionState = {
        currentSubAction: 0,
        skippedActions: 0,
      }
    } else if (takeTurn.selectionState.selection === undefined) {
      const selection = ui.selectedAction()
      if (selection !== undefined) {
        takeTurn.selectionState.selection = selection
        ui.hideSelectors()
      }
    } else {
      const skip = input.isActive('cancel')
      const action = takeTurn.selectionState.selection!.action
      const subActionCount = action.subActions.length
      const allDone = takeTurn.selectionState.currentSubAction >= subActionCount
      if (!allDone) {
        if (skip) {
          takeTurn.selectionState.currentSubAction += 1
          takeTurn.selectionState.skippedActions += 1
        } else {
          const currentSubAction = action.subActions[takeTurn.selectionState.currentSubAction]
          const done = this.handleSubAction(world, entity, action, currentSubAction, takeTurn.selectionState)
          if (done) {
            takeTurn.selectionState.currentSubAction += 1
          }
        }
      } else {
        clearExpiredEffects(world, entity)
        const action = takeTurn.selectionState.selection!.action
        const noActionsOrAtLeastOneActionTaken = subActionCount == 0 || subActionCount - takeTurn.selectionState.skippedActions > 0
        if (noActionsOrAtLeastOneActionTaken) {
          switch (action.cost) {
            case 'action':
              takeTurn.acted = true
              break
            case 'movement':
              takeTurn.moved = true
              break
            case 'either':
              if (takeTurn.moved) {
                takeTurn.acted = true
              } else {
                takeTurn.moved = true
              }
              break
            case 'all':
            case 'both':
              takeTurn.acted = true
              takeTurn.moved = true
              break
          }
        }
        takeTurn.selectionState = undefined
      }
    }
  }

  public handleSubAction(world: TlbWorld, entity: Entity, action: Action, subAction: SubAction, state: SelectionState): boolean {
    switch (subAction.kind) {
      case 'attack':
        if (state.target === undefined) {
          state.target = this.findTarget(world, entity, subAction)
          return false
        } else {
          const result = attackTarget(world, this.random, entity, state.target!, subAction)
          if (subAction.grants) {
            action.subActions.splice(state.currentSubAction + 1, 0, ...subAction.grants(result))
          }
          state.target = undefined
          return true
        }
      case 'movement':
      case 'jump':
        return this.move(world, entity, subAction)
      case 'status':
        // status is not actively skippable
        state.skippedActions += 1
        return this.status(world, entity, subAction, state.selection!.entity)
      case 'changeEquipment':
        return this.changeEquipment(world, entity)
      case 'trigger':
        return this.trigger(world, entity, state.selection!.entity)
    }
  }

  public endTurn(world: TlbWorld, entity: Entity): void {
    world
      .editEntity(entity)
      .removeComponent('take-turn')
      .withComponent('took-turn', {})
  }

  public move(world: TlbWorld, entity: Entity, movement: Movement | Jump): boolean {
    const ui = world.getResource<UIResource>('ui')
    ui.showMovementSelector(entity, this.queries, movement)
    const path = ui.selectedMovement()
    if (path !== undefined) {
      ui.hideSelectors()
      world.editEntity(entity).withComponent<ScriptComponent>('script', {
        path: path.path,
      })
      return true
    }
    return false
  }

  public findTarget(world: TlbWorld, entity: Entity, attack: Attack): Entity | undefined {
    const viewport: Viewport = world.getResource<ViewportResource>('viewport')
    const map: WorldMap = world.getResource<WorldMapResource>('map')
    const ui = world.getResource<UIResource>('ui')
    ui.showAttackSelector(entity, this.queries, attack.range)
    const path = ui.selectedAttack()
    if (path !== undefined) {
      for (let i = 0; i < path.path.length; ++i) {
        const region = map.levels[viewport.level]
        let target = region.getCharacter(path.path[i])
        const hasEnemy = target !== undefined && target !== entity
        if (hasEnemy) {
          ui.hideSelectors()
          return target
        }
      }
    }
    return undefined
  }

  public status(world: TlbWorld, entity: Entity, status: Status, item: Entity): boolean {
    const target = status.target === 'item' ? item : entity
    status.effects.forEach(effect => {
      applyEffect(world, this.random, target, { source: item, effect, isCritical: false, isPiercing: false })
    })
    const itemComponent = world.getComponent<ItemComponent>(item, 'item')
    if (itemComponent !== undefined && itemComponent.item.kind === 'consumable') {
      consumeItem(world, entity, item)
    }
    return true
  }

  public trigger(world: TlbWorld, entity: Entity, target: Entity): boolean {
    world
      .editEntity(target)
      .withComponent('active', {})
      .withComponent<TriggeredByComponent>('triggered-by', { entity })
    return true
  }

  public changeEquipment(world: TlbWorld, entity: Entity): boolean {
    const ui = world.getResource<UIResource>('ui')
    let selection: Entity | undefined = undefined
    const inventory = createInventoryDescription(world, entity)
    if (!ui.multipleChoiceModalShowing()) {
      const items = inventory.items
        .filter(i => i.item.attachments > 0)
        .map(i => ({
          entity: i.entity,
          description: i.item.name + (inventory.equipment.some(e => e.entity === i.entity) ? '*' : ''),
        }))
      ui.showMultipleChoiceModal(world, 'choose item', items)
      this.pushState(new Modal(world.activeSystemsList()))
    } else {
      selection = ui.selectedModalOption()
      ui.hideMultipleChoiceModal()
    }
    if (selection !== undefined) {
      const { item } = instantiateItem(world, selection)
      const attachementIndex = inventory.equipment.findIndex(e => e.entity === selection)

      if (attachementIndex >= 0) {
        unequip(world, entity, selection)
      } else {
        replacingEquip(world, entity, selection, item)
      }
      return true
    }
    return false
  }
}
