import { Partitioner, Partition } from './Partitioner'
import { Difference } from '../../geometry/difference'
import { Random } from '../../random'
import { Shape } from '../../geometry/shape'
import { Vector } from '../../spatial'
import { SpacePartitioner } from '../space-partitioner'

export class OutsetCorridorSplit implements Partitioner {
  public constructor(private readonly outerWidth: number, private readonly minInnerWidth: number) {}

  public weight = 1

  public canPartition(shape: Shape): boolean {
    const bounds = shape.bounds()
    const width = Math.min(bounds.width, bounds.height)
    return width - this.outerWidth - 1 > this.minInnerWidth
  }

  public partition(context: SpacePartitioner, _random: Random, shape: Shape, exits: Vector[]): Partition {
    const inner = shape.shrink(this.outerWidth + 1)
    const outer = new Difference(shape, inner.grow())
    const leftExit = inner.bounds().grow().centerLeft
    return {
      shape,
      type: 'node',
      exit: 1,
      partitions: [context.partition(inner, [leftExit]), context.createCorridor(outer, exits)],
      connections: [{ from: 0, to: 1 }],
    }
  }
}
