import { TlbWorld } from '../tlb'
import { Entity } from '../ecs/entity'
import { TriggeredByComponent, TriggersComponent } from '../components/trigger'
import { Shape } from '../geometry/shape'
import { WorldMapResource } from '../resources/world-map'

export function findTriggers(world: TlbWorld, level: number, shape: Shape): { entity: Entity; component: TriggersComponent }[] {
  const map = world.getResource<WorldMapResource>('map')
  const seen: Set<Entity> = new Set()
  const triggers: { entity: Entity; component: TriggersComponent }[] = []
  shape.foreach(p => {
    const tile = map.levels[level].getTile(p)
    if (tile !== undefined) {
      registerTrigger(world, seen, tile, triggers)
    }
    const character = map.levels[level].getCharacter(p)
    if (character !== undefined) {
      registerTrigger(world, seen, character, triggers)
    }
  })
  return triggers
}

function registerTrigger(
  world: TlbWorld,
  seen: Set<Entity>,
  owner: Entity,
  triggers: { entity: Entity; component: TriggersComponent }[]
): void {
  const entity = findTriggerOfEntity(world, owner)
  if (entity !== undefined && !seen.has(entity)) {
    seen.add(entity)
    const component = world.getComponent<TriggersComponent>(entity, 'triggers')!
    triggers.push({ entity, component })
  }
}

export function findTriggerOfEntity(world: TlbWorld, entity: Entity): Entity | undefined {
  if (world.getComponent(entity, 'triggers') !== undefined) {
    return entity
  }
  const triggeredBy = world.getComponent<TriggeredByComponent>(entity, 'triggered-by')
  if (triggeredBy !== undefined) {
    if (world.getComponent<TriggersComponent>(triggeredBy.entity, 'triggers') !== undefined) {
      return triggeredBy.entity
    }
  }
  return undefined
}
