import { Entity } from '../ecs/entity'
import { Effect, ActiveEffectsComponent } from './effects'
import { ItemBaseType, createItem } from '../assets/items'
import { Action } from './action'
import { RegionsType } from './region'
import { Random } from '../random'
import { TlbWorld } from '../tlb'
import { getRandomMod } from '../assets/mods'

export type WearableKind = 'glove' | 'helmet' | 'armor' | 'pants' | 'boots'
export type ItemKind = 'scrap' | 'consumable' | 'weapon' | WearableKind

export type ItemTag = 'rifle' | 'high-tech' | 'melee' | 'gun'

export type ModAction =
  | 'overcharge'
  | 'doubleShot'
  | 'frenzy'
  | 'headshot'
  | 'execute'
  | 'ranged'
  | 'stabilizing'
  | 'bloody'
  | 'acrobatics'
  | 'stunning'
  | 'penetrating'
  | 'mobile'
  | 'enhanced'
  | 'fast'

export type ModKind = 'passive' | 'active'

export interface Mod {
  kind: ModKind
  adjective: string
  occurs: RegionsType[]

  action?: ModAction
  effects?: Effect[]

  matches: {
    kind: ItemKind
    tags: ItemTag[]
  }[]
}

export interface Item {
  base: ItemBaseType
  tags: ItemTag[]

  name: string
  shortName: string
  description: string

  actions: Action[]
  effects: Effect[]

  kind: ItemKind
  weight: number
  attachments: number
}

export interface InventoryComponent {
  content: Entity[]
}

export interface ItemComponent {
  item: Item
  mods: Mod[]
}

export interface EquipmentAttachement {
  entity: Entity

  bodyParts: string[]
}

export interface EquipedItemsComponent {
  equipment: EquipmentAttachement[]
}

export function createEntityWithItemComponent(world: TlbWorld, base: ItemBaseType, random: Random, region: RegionsType): Entity {
  const item = createItem(base, random, region)
  const mods = []
  const passive = getRandomMod(random, 'passive', item, region)
  if (passive) {
    mods.push(passive)
  }
  const active = getRandomMod(random, 'active', item, region)
  if (active) {
    mods.push(active)
  }
  return world
    .createEntity()
    .withComponent<ItemComponent>('item', { item, mods })
    .withComponent<ActiveEffectsComponent>('active-effects', { effects: [] }).entity
}
