import { Partitioner, Partition } from './Partitioner'

import { SpacePartitioner } from '../space-partitioner'
import { Shape } from '../../geometry/shape'
import { Axis } from '../../spatial/axis'
import { Vector } from '../../spatial'
import { Rectangle } from '../../geometry/rectangle'
import { Random } from '../../random'

export class RoomSplit implements Partitioner {
  public constructor(private readonly minRoomWidth: number) {}

  public weight = 10

  public canPartition(shape: Shape, exits: Vector[]): boolean {
    const exit = exits[0]
    const bounds = shape.bounds().grow()
    const axis: Axis = bounds.top === exit.y || bounds.bottom === exit.y ? 'y' : 'x'
    const sideLength = axis === 'x' ? bounds.width : bounds.height
    return sideLength > 2 * this.minRoomWidth
  }

  public partition(context: SpacePartitioner, random: Random, shape: Shape, exits: Vector[]): Partition {
    const [head, ...tail] = exits
    const bounds = shape.bounds()
    const axis: Axis = bounds.grow().top === head.y || bounds.grow().bottom === head.y ? 'y' : 'x'
    const sideLength = axis === 'x' ? bounds.width : bounds.height
    const split = random.integerBetween(this.minRoomWidth, sideLength - this.minRoomWidth)
    let [left, right] = this.split(bounds, axis, split)
    let exit = axis === 'x' ? left.grow().centerRight : left.grow().centerBottom
    if (context.exitConnectsShape(right, head)) {
      ;[left, right] = [right, left]
    }
    const leftTail = tail.filter(e => context.exitConnectsShape(left, e))
    const rightTail = tail.filter(e => context.exitConnectsShape(right, e))
    return {
      shape,
      type: 'node',
      exit: 0,
      partitions: [context.partition(left, [head, exit, ...leftTail]), context.partition(right, [exit, ...rightTail])],
      connections: [{ from: 0, to: 1 }],
    }
  }

  public split(r: Rectangle, axis: Axis, split: number): [Rectangle, Rectangle] {
    let a: Rectangle
    let b: Rectangle
    if (axis === 'x') {
      a = Rectangle.fromBounds(r.left, r.left + split, r.top, r.bottom)
      b = Rectangle.fromBounds(a.right + 2, r.right, r.top, r.bottom)
    } else {
      a = Rectangle.fromBounds(r.left, r.right, r.top, r.top + split)
      b = Rectangle.fromBounds(r.left, r.right, a.bottom + 2, r.bottom)
    }
    return [a, b]
  }
}
