import { Partitioner, Partition } from './Partitioner'
import { Random } from '../../random'
import { Shape } from '../../geometry/shape'
import { Vector } from '../../spatial'
import { SpacePartitioner } from '../space-partitioner'
import { Rectangle } from '../../geometry/rectangle'
import { Difference } from '../../geometry/difference'
import { Union } from '../../geometry/union'

/*
          1         2     
01234567890123456789012345
#########################0
#33333333333333333333322#1
#33333333333333333333322#2
#44###################22#3
#44#66777777777777777#22#5
#44#66777777777777777#22#5
#44#66#############88#22#6
#44#66#33333333322#88#22#7
#44#66#33333333322#88#22#8
#44#66#44#######22#88#22#9
#44#66#44#CCCCC#22#88#22#01
#44#66#44#CCCCC#22#88#22#1
#44#66#44#CCCCC#22#88#22#2
#44#55544#CCCCC111#88#22#3
#44#55544#CCCCC111#88#22#4
#44################88#22#5
#4455555555555#999988#22#6
#4455555555555#999988#22#7
############66#00#####22#8
            66#001111111#9
            66#001111111#0
           ###############1
*/

export class KnossosMaze implements Partitioner {
  public weight = 1

  public canPartition(_shape: Shape): boolean {
    return true
  }

  public partition(context: SpacePartitioner, _random: Random, shape: Shape, exits: Vector[]): Partition {
    const corridorWidth = 2
    const wallWidth = 1
    const center = new Rectangle(shape.bounds().center.x - 5, shape.bounds().center.y, 10, 10)
    const c1 = new Rectangle(center.bottomRight.x + 1, center.bottomRight.y - corridorWidth + 1, corridorWidth + wallWidth, corridorWidth)
    const c2 = new Rectangle(
      center.topRight.x + wallWidth + 1,
      center.topRight.y - corridorWidth - wallWidth,
      corridorWidth,
      wallWidth + center.height
    )
    const c3 = new Rectangle(
      center.topLeft.x - wallWidth - corridorWidth,
      center.topLeft.y - corridorWidth - wallWidth,
      corridorWidth + 2 * wallWidth + center.width,
      corridorWidth
    )
    const c4 = new Rectangle(
      center.topLeft.x - wallWidth - corridorWidth,
      center.topLeft.y - wallWidth,
      corridorWidth,
      center.height + wallWidth
    )
    const c5 = new Rectangle(
      center.bottomLeft.x - 2 * corridorWidth - 2 * wallWidth,
      center.bottomLeft.y - corridorWidth + 1,
      corridorWidth + wallWidth,
      corridorWidth
    )
    const c6 = new Rectangle(
      center.topLeft.x - 2 * corridorWidth - 2 * wallWidth,
      center.topLeft.y - 2 * corridorWidth - 2 * wallWidth,
      corridorWidth,
      center.height - corridorWidth + 2 * (wallWidth + corridorWidth)
    )
    const c7 = new Rectangle(
      center.topLeft.x - corridorWidth - 2 * wallWidth,
      center.topLeft.y - 2 * corridorWidth - 2 * wallWidth,
      center.width + 3 * corridorWidth + 4 * wallWidth,
      corridorWidth
    )
    const c8 = new Rectangle(
      center.topRight.x + corridorWidth + 2 * wallWidth + 1,
      center.topRight.y - corridorWidth - 2 * wallWidth,
      corridorWidth,
      center.height + 2 * corridorWidth + 3 * wallWidth
    )
    const c9 = new Rectangle(center.bottomRight.x + 1, center.bottomRight.y + wallWidth + 1, corridorWidth + 2 * wallWidth, corridorWidth)
    const c10 = new Rectangle(
      center.bottomRight.x + 1,
      center.bottomRight.y + wallWidth + 1 + corridorWidth,
      corridorWidth,
      corridorWidth + wallWidth
    )
    const c11 = new Rectangle(
      center.bottomRight.x + 1 + corridorWidth,
      center.bottomRight.y + 2 * wallWidth + 1 + corridorWidth,
      2 * corridorWidth + 3 * wallWidth,
      corridorWidth
    )
    const c12 = new Rectangle(
      center.topRight.x + 2 * corridorWidth + 3 * wallWidth + 1,
      center.topRight.y - 3 * corridorWidth - 3 * wallWidth,
      corridorWidth,
      center.height + 4 * corridorWidth + 5 * wallWidth
    )
    const c13 = new Rectangle(
      center.topLeft.x - 3 * corridorWidth - 3 * wallWidth,
      center.topLeft.y - 3 * corridorWidth - 3 * wallWidth,
      center.width + 5 * corridorWidth + 6 * wallWidth,
      corridorWidth
    )
    const c14 = new Rectangle(
      center.topLeft.x - 3 * corridorWidth - 3 * wallWidth,
      center.topLeft.y - 2 * corridorWidth - 3 * wallWidth,
      corridorWidth,
      center.height + 3 * corridorWidth + 4 * wallWidth
    )
    const c15 = new Rectangle(
      center.topLeft.x - 2 * corridorWidth - 3 * wallWidth,
      center.bottomRight.y + wallWidth + 1,
      center.width + 2 * corridorWidth + 2 * wallWidth,
      corridorWidth
    )
    const c16 = new Rectangle(
      center.bottomRight.x + 1 - wallWidth - corridorWidth,
      center.bottomRight.y + wallWidth + 1 + corridorWidth,
      corridorWidth,
      corridorWidth + wallWidth
    )
    const maze = Union.of(center, c1, c2, c3, c4, c5, c6, c7, c8, c9, c10, c11, c12, c13, c14, c15, c16)
    const outer = new Difference(shape, maze.grow())
    return {
      shape,
      type: 'node',
      exit: 1,
      partitions: [
        context.createRoom(center, [c1.grow().centerLeft]),
        context.createCorridor(c1, [c2.grow().centerBottom]),
        context.createCorridor(c2, [c3.grow().centerRight]),
        context.createCorridor(c3, [c4.grow().centerTop]),
        context.createCorridor(c4, [c5.grow().centerRight]),
        context.createCorridor(c5, [c6.grow().centerBottom]),
        context.createCorridor(c6, [c7.grow().centerLeft]),
        context.createCorridor(c7, [c8.grow().centerTop]),
        context.createCorridor(c8, [c9.grow().centerRight]),
        context.createCorridor(c9, [c10.grow().centerTop]),
        context.createCorridor(c10, [c11.grow().centerLeft]),
        context.createCorridor(c11, [c12.grow().centerBottom]),
        context.createCorridor(c12, [c13.grow().centerRight]),
        context.createCorridor(c13, [c14.grow().centerLeft]),
        context.createCorridor(c14, [c15.grow().centerLeft]),
        context.createCorridor(c15, [c16.grow().centerTop]),
        context.createCorridor(c16, [c16.grow().centerBottom]),
        context.createCorridor(outer, exits),
      ],
      connections: [
        { from: 0, to: 1 },
        { from: 1, to: 2 },
        { from: 2, to: 3 },
        { from: 3, to: 4 },
        { from: 4, to: 5 },
        { from: 5, to: 6 },
        { from: 6, to: 7 },
        { from: 7, to: 8 },
        { from: 8, to: 9 },
        { from: 9, to: 10 },
        { from: 10, to: 11 },
        { from: 11, to: 12 },
        { from: 12, to: 13 },
        { from: 13, to: 14 },
        { from: 14, to: 15 },
        { from: 15, to: 16 },
      ],
    }
  }
}
