import { ResourceName, TlbResource, TlbWorld } from '../tlb'
import { Renderer } from '../renderer/renderer'
import { getFeature } from '../components/feature'
import { Entity } from '../ecs/entity'
import { AttackResult } from '../component-reducers/attack-target'

export interface Log {
  getEntries(offset: number, limit: number): string[]

  text(text: string): void

  attack(world: TlbWorld, source: Entity, target: Entity, result: AttackResult): void
  died(world: TlbWorld, entity: Entity): void
}

export class LogResource implements TlbResource, Log {
  public readonly kind: ResourceName = 'log'

  entries: string[] = []

  public update({}: TlbWorld): void {}

  public render({}: Renderer): void {}

  public getEntries(offset: number, limit: number): string[] {
    const start = Math.max(0, this.entries.length + offset - limit)
    const end = Math.min(this.entries.length, start + limit)
    return this.entries.slice(start, end)
  }

  public text(text: string): void {
    this.entries.push(text)
  }

  public attack(world: TlbWorld, source: Entity, target: Entity, result: AttackResult): void {
    const subject = this.getName(world, source)!
    const object = this.getName(world, target)!

    let attackText = `${subject} ${this.verbify(subject, 'try')} to hit ${object} (${this.percentage(
      result.chance.final
    )} chance, ${this.percentage(result.chance.criticalHit)} crit)`

    let hitText = `${subject} ${result.isCritical ? 'critically' : ''} ${
      result.isHit ? this.verbify(subject, 'hit') : this.verbify(subject, 'miss')
    } ${object}`
    let applyText = `${subject} ${this.verbify(subject, 'make')} ${object}`
    this.entries.push(attackText)
    result.effects.forEach(e => {
      if (e.type === 'damage') {
        this.entries.push(`${hitText} ${e.finalDamage}${result.isCritical ? '!' : ''}dmg (${e.defense}def)`)
      } else {
        this.entries.push(`${applyText} ${e.type}`)
      }
    })
  }

  public died(world: TlbWorld, entity: Entity): void {
    const subject = this.getName(world, entity)!
    this.entries.push(`${subject} died`)
  }

  public percentage(value: number): string {
    return `${Math.floor(value * 100)}%`
  }

  public getName(world: TlbWorld, entity: Entity): string {
    const feature = getFeature(world, entity)
    if (feature !== undefined) {
      return feature.name
    }
    return 'something'
  }

  public verbify(source: string, verb: string): string {
    if (source !== 'you') {
      if (verb === 'miss') {
        return 'misses'
      }
      if (verb === 'try') {
        return 'tries'
      }
      return verb + 's'
    }
    return verb
  }

  public owner(target: string): string {
    if (target === 'you') {
      return 'your'
    }
    return `${target}'s`
  }
}
