import { TlbWorld } from '../tlb'
import { EffectApplication, ActiveEffectsComponent, damage, effect } from '../components/effects'
import { Entity } from '../ecs/entity'
import { CharacterStatsComponent } from '../components/character-stats'
import { kill } from './kill'
import { Random } from '../random'
import { getCriticalMultiplier, getDefense } from './stats'
import { characterStats } from '../assets/characters'

export interface EffectResult {
  type: effect
  killed?: boolean
  baseDamage?: number
  defense?: number
  finalDamage?: number
}

export function applyEffect(world: TlbWorld, uniform: Random, target: Entity, application: EffectApplication): EffectResult {
  const type = application.effect.type
  let result: Partial<EffectResult>
  switch (application.effect.type) {
    case 'damage':
      result = applyDamage(world, uniform, target, application)
      break
    case 'kill':
      kill(world, uniform, target)
      result = { killed: true }
      break
    case 'negate':
      result = removeAllNegativeEffects(world, target)
      break
    case 'heal':
      result = applyHeal(world, target, application)
      break
    default:
      result = applyStatusEffect(world, target, application)
      break
  }
  return { type, ...result }
}

export function clearExpiredEffects(world: TlbWorld, entity: Entity): void {
  const activeEffects = world.getComponent<ActiveEffectsComponent>(entity, 'active-effects')!
  activeEffects.effects = activeEffects.effects.filter(e => e.effect.duration === undefined || e.effect.duration > 0)
}

export function updateEffects(world: TlbWorld, entity: Entity, uniform: Random): void {
  const activeEffects = world.getComponent<ActiveEffectsComponent>(entity, 'active-effects')!
  activeEffects.effects.forEach(effect => {
    switch (effect.effect.type) {
      case 'bleed':
        applyEffect(world, uniform, entity, { source: entity, effect: damage(1), isCritical: false, isPiercing: true })
        break
    }
    if (effect.effect.duration !== undefined && effect.effect.duration > 0) {
      effect.effect.duration -= 1
    }
  })
}

export function applyDamage(world: TlbWorld, uniform: Random, target: Entity, application: EffectApplication): Partial<EffectResult> {
  const { effect } = application
  const stats = world.getComponent<CharacterStatsComponent>(target, 'character-stats')
  if (stats === undefined) {
    return { killed: false }
  }

  let killed = false
  const defense = application.isPiercing ? 0 : getDefense(world, target)
  const criticalMultiplier = application.isCritical ? getCriticalMultiplier(world, target) : 1.0
  const defenseMultiplier = 10 / (10 + defense)
  const finalDamage = Math.floor(Math.max(0, effect.value! * defenseMultiplier * criticalMultiplier))
  if (finalDamage > 0) {
    stats.current.health = Math.max(0, stats.current.health - finalDamage)
    killed = stats.current.health === 0
  }

  if (killed) {
    kill(world, uniform, target)
  }

  return { killed, baseDamage: effect.value, finalDamage, defense }
}

export function applyHeal(world: TlbWorld, target: Entity, application: EffectApplication): Partial<EffectResult> {
  const stats = world.getComponent<CharacterStatsComponent>(target, 'character-stats')
  if (stats !== undefined) {
    stats.current.health += application.effect.value || characterStats[stats.type].health
    stats.current.health = Math.min(characterStats[stats.type].health)
  }
  return {}
}

export function applyStatusEffect(world: TlbWorld, target: Entity, application: EffectApplication): Partial<EffectResult> {
  const activeEffects = world.getComponent<ActiveEffectsComponent>(target, 'active-effects')!
  activeEffects.effects.push(application)
  return {}
}

export function removeAllNegativeEffects(world: TlbWorld, target: Entity): Partial<EffectResult> {
  const activeEffects = world.getComponent<ActiveEffectsComponent>(target, 'active-effects')!
  activeEffects.effects = activeEffects.effects.filter(e => !e.effect.negative)
  return {}
}
