import { createFeatureFromType } from '../components/feature'
import { RegionComponent, StructureComponent } from '../components/region'
import { Entity } from '../ecs/entity'
import { Distribution } from '../random/distributions'
import { WorldMap, WorldMapResource } from '../resources/world-map'
import { ComponentName, TlbSystem, TlbWorld } from '../tlb'
import { shapeOfAsset, createAssetFromShape } from '../components/asset'
import { regionParams } from '../assets/complexes'
import { embedComplexes, ComplexEmbedding } from '../generative/complex-embedder'
import { fill } from '../generative/complex-filler'
import { SpacePartitioner } from '../generative/space-partitioner'
import { FeatureType } from '../assets/features'
import { Shape } from '../geometry/shape'
import { Vector } from '../spatial'
import { Partition } from '../generative/partitioners/Partitioner'
import * as partitioners from '../generative/partitioners'
import { Random } from '../random'

export class RegionBuilder implements TlbSystem {
  public readonly components: ComponentName[] = ['active', 'region']

  private readonly uniform: Random
  public constructor(private readonly rng: Distribution, public readonly dry: boolean = false) {
    this.uniform = new Random(rng)
  }

  public update(world: TlbWorld, entity: Entity): void {
    const region = world.getComponent<RegionComponent>(entity, 'region')!
    let success = true
    switch (region.type) {
      case 'entrance':
        success = this.buildEntrance(world, entity, region)
        break
      case 'ground-floor':
        success = this.buildOfficeSpace(world, entity, region)
        break
      case 'elevator':
        success = this.buildElevator(world, entity, region)
        break
      case 'red-base':
      case 'red-plus':
      case 'green-base':
      case 'green-plus':
      case 'yellow-base':
      case 'yellow-plus':
      case 'blue-base':
      case 'blue-plus':
        success = this.buildOfficeSpace(world, entity, region)
        break
      case 'core':
        success = this.buildCore(world, entity, region)
        break
    }
    if (success) {
      world.editEntity(entity).removeComponent('active')
    } else {
      console.error('region failed', region)
    }
  }

  private buildElevator(world: TlbWorld, entity: Entity, region: RegionComponent): boolean {
    const partition: Partition = {
      type: 'final',
      kind: 'hub',
      shape: region.shape.shrink(),
      exits: region.exits,
    }
    return this.tryToBuildPartition(world, entity, region, partition)
  }

  private buildOfficeSpace(world: TlbWorld, entity: Entity, region: RegionComponent): boolean {
    const partitioner = new SpacePartitioner(this.rng)
    partitioner.registerPartitioner(new partitioners.CorridorSplit(3, 10))
    partitioner.registerPartitioner(new partitioners.RoomSplit(8))
    partitioner.registerPartitioner(new partitioners.OutsetCorridorSplit(3, 8))
    const partition = partitioner.partition(region.shape, region.exits)
    return this.tryToBuildPartition(world, entity, region, partition)
  }

  private buildEntrance(world: TlbWorld, entity: Entity, region: RegionComponent): boolean {
    const partitioner = new SpacePartitioner(this.rng)
    partitioner.registerPartitioner(new partitioners.OutsetCorridorSplit(3, 8))
    const partition = partitioner.partition(region.shape, region.exits)
    return this.tryToBuildPartition(world, entity, region, partition)
  }

  private buildCore(world: TlbWorld, entity: Entity, region: RegionComponent): boolean {
    const partitioner = new SpacePartitioner(this.rng)
    partitioner.registerPartitioner(new partitioners.KnossosMaze())
    const partition = partitioner.partition(region.shape, region.exits)
    return this.tryToBuildPartition(world, entity, region, partition)
  }

  private printPartition(partition: Partition, tab: string = ''): string {
    switch (partition.type) {
      case 'final': {
        const bounds = partition.shape.bounds()
        return `${tab}+${partition.kind} - ${bounds.x}, ${bounds.y}, ${bounds.width}, ${bounds.height}`
      }
      case 'node': {
        const bounds = partition.shape.bounds()
        return `${tab}+${partition.type} - ${bounds.x}, ${bounds.y}, ${bounds.width}, ${bounds.height}\n${partition.partitions
          .map(p => this.printPartition(p, tab + '| '))
          .join('\n')}`
      }
    }
  }
  private tryToBuildPartition(world: TlbWorld, entity: Entity, region: RegionComponent, partition: Partition): boolean {
    const root = this.fillPartitionWithStructure(world, entity, partition)
    const embeddings = this.findEmbedding(world, entity, root)

    if (embeddings !== undefined) {
      if (!this.dry) {
        const map = world.getResource<WorldMapResource>('map')
        this.renderPartition(world, map, region.level, partition)
        embeddings.forEach(e => fill(world, map, region.level, e.embedding, this.uniform, e.structure))
      }
      return true
    } else {
      this.removeStructures(world, entity)
      return false
    }
  }

  public fillPartitionWithStructure(world: TlbWorld, region: Entity, partition: Partition): Entity {
    switch (partition.type) {
      case 'node': {
        const structures = partition.partitions.map(p => this.fillPartitionWithStructure(world, region, p))
        partition.connections.forEach(e => {
          const from = structures[e.from]
          const to = structures[e.to]
          world.getComponent<StructureComponent>(from, 'structure')!.connections.push(to)
          world.getComponent<StructureComponent>(to, 'structure')!.connections.push(from)
        })
        return structures[partition.exit]
      }
      case 'final': {
        const entity = world
          .createEntity()
          .withComponent<StructureComponent>('structure', { kind: partition.kind, shape: partition.shape, connections: [], region }).entity
        partition.entity = entity
        return entity
      }
    }
  }

  public removeStructures(world: TlbWorld, region: Entity): void {
    const structures: Entity[] = []
    world.getStorage<StructureComponent>('structure').foreach((e, s) => {
      if (s.region === region) {
        structures.push(e)
      }
    })
    structures.forEach(e => world.deleteEntity(e))
  }

  public findEmbedding(world: TlbWorld, entity: Entity, structure: Entity): ComplexEmbedding[] | undefined {
    const s = world.getComponent<StructureComponent>(structure, 'structure')!
    if (s.region === entity) {
      const region = world.getComponent<RegionComponent>(entity, 'region')!
      const params = regionParams[region.type]
      return embedComplexes(world, this.uniform, entity, params)
    }
    return []
  }

  private renderPartition(world: TlbWorld, map: WorldMap, level: number, partition: Partition): void {
    switch (partition.type) {
      case 'node': {
        partition.partitions.map(partition => this.renderPartition(world, map, level, partition))
        break
      }
      case 'final': {
        const entity = partition.entity!
        if (partition.kind === 'room') {
          this.renderRoom(world, map, level, entity, partition.shape, partition.kind)
          this.renderExits(world, map, level, entity, partition.shape, partition.exits)
          this.renderDoors(world, map, level, partition.shape, partition.exits)
        } else if (partition.kind === 'corridor') {
          this.renderRoom(world, map, level, entity, partition.shape, partition.kind)
          this.renderExits(world, map, level, entity, partition.shape, partition.exits)
        } else if (partition.kind === 'hub') {
          this.renderRoom(world, map, level, entity, partition.shape, partition.kind)
          this.renderExits(world, map, level, entity, partition.shape, partition.exits)
          this.renderDoors(world, map, level, partition.shape, partition.exits)
        }
      }
    }
  }

  public renderExits(world: TlbWorld, map: WorldMap, level: number, entity: Entity, shape: Shape, positions: Vector[]): void {
    const bounds = shape.bounds()
    positions.forEach(exit => {
      if (map.levels[level].getStructure(exit) === undefined) {
        const direction = bounds.grow().getWall(exit)
        if (direction !== undefined) {
          const shape = shapeOfAsset('door', exit, direction!)
          shape.foreach(position => {
            this.setTile(world, map, level, position, entity, 'corridor')
          })
        }
      }
    })
  }

  public renderDoors(world: TlbWorld, map: WorldMap, level: number, shape: Shape, positions: Vector[]): void {
    const bounds = shape.bounds()
    positions.forEach(exit => {
      if (map.levels[level].tileMatches(world, exit, f => f !== undefined && f.feature().ground)) {
        const direction = bounds.grow().getWall(exit)
        if (direction !== undefined) {
          const shape = shapeOfAsset('door', exit, direction!)
          createAssetFromShape(world, this.uniform, level, shape, 'door')
        }
      }
    })
  }

  private renderRoom(world: TlbWorld, map: WorldMap, level: number, entity: Entity, shape: Shape, floor: FeatureType): void {
    shape.foreach(position => {
      this.setTile(world, map, level, position, entity, floor)
    })
  }

  private setTile(world: TlbWorld, map: WorldMap, level: number, position: Vector, entity: Entity, floor: FeatureType): void {
    createFeatureFromType(world, level, position, floor)
    map.levels[level].setStructure(position, entity)
  }
}
