import { Item, ItemComponent, Mod } from '../components/items'
import { copy } from '../object-utils'
import { addModification } from '../assets/actions'
import { TlbWorld } from '../tlb'
import { Entity } from '../ecs/entity'
import { ActiveEffectsComponent } from '../components/effects'

export function instantiateItem(world: TlbWorld, entity: Entity): { item: Item; mods: Mod[] } {
  const component = world.getComponent<ItemComponent>(entity, 'item')!
  const activeEffects = world.getComponent<ActiveEffectsComponent>(entity, 'active-effects')!
  const item = copy(component.item)
  if (component.mods.length > 0) {
    let adjective: string | undefined = undefined
    component.mods.forEach(m => {
      if (m.kind === 'active' || adjective === undefined) {
        adjective = m.adjective
      }
    })
    item.name = `${adjective} ${item.name}`
    if (item.actions.length > 0) {
      const passiveAction = component.mods
        .filter(mod => mod.kind === 'passive' && mod.action !== undefined)
        .reduce((action, mod) => addModification(mod.action!, action), item.actions[0])
      item.actions = [passiveAction]

      const cooldown = activeEffects.effects.find(e => e.effect.type === 'cooldown')
      component.mods
        .filter(mod => mod.kind === 'active' && mod.action !== undefined)
        .forEach(mod => {
          const modAction = addModification(mod.action!, passiveAction)
          if (cooldown) {
            modAction.cooldown = cooldown.effect.duration
          }
          item.actions.push(modAction)
        })
    }
    component.mods.filter(mod => mod.effects !== undefined).forEach(mod => item.effects.push(...mod.effects!))
  }
  return { item, mods: component.mods }
}
