import { Entity } from '../ecs/entity'

export type effect =
  | 'damage'
  | 'cooldown'
  | 'cooldown-buff'
  | 'kill'
  | 'stun'
  | 'bleed'
  | 'confuse'
  | 'defense'
  | 'immobilize'
  | 'negate'
  | 'heal'
  | 'aim'
  | 'crit-chance'
  | 'crit-multiplier'

export interface Effect {
  type: effect
  negative: boolean

  duration?: number
  value?: number
}

export interface ActiveEffectsComponent {
  effects: EffectApplication[]
}

export interface EffectApplication {
  source: Entity
  effect: Effect
  isCritical: boolean
  isPiercing: boolean
}

export function damage(value: number): Effect {
  return { type: 'damage', negative: true, value }
}

export function cooldown(duration: number): Effect {
  return { type: 'cooldown', negative: true, duration }
}

export function cooldownBuff(value: number, duration: number | undefined): Effect {
  return { type: 'cooldown-buff', negative: true, value, duration }
}

export function confuse(duration: number): Effect {
  return { type: 'confuse', negative: true, duration }
}

export function defend(value: number, duration: number | undefined): Effect {
  return { type: 'defense', negative: false, duration, value }
}

export function penetrate(value: number, duration: number | undefined): Effect {
  return { type: 'defense', negative: true, duration, value: -value }
}

export function stun(duration: number): Effect {
  return { type: 'stun', negative: true, duration }
}

export function immobilize(duration: number): Effect {
  return { type: 'immobilize', negative: true, duration }
}

export function aim(value: number, duration: number | undefined): Effect {
  return { type: 'aim', negative: false, duration, value }
}

export function critChance(value: number, duration: number | undefined): Effect {
  return { type: 'crit-chance', negative: false, duration, value }
}

export function critMultiplier(value: number, duration: number | undefined): Effect {
  return { type: 'crit-multiplier', negative: false, duration, value }
}

export function bleed(): Effect {
  return { type: 'bleed', negative: true }
}

export function kill(): Effect {
  return { type: 'kill', negative: true }
}

export function negateEffects(): Effect {
  return { type: 'negate', negative: false }
}

export function heal(): Effect {
  return { type: 'heal', negative: false }
}
