import { Partitioner, Partition } from './Partitioner'

import { SpacePartitioner } from '../space-partitioner'
import { Shape } from '../../geometry/shape'
import { Vector } from '../../spatial'
import { Axis } from '../../spatial/axis'
import { Random } from '../../random'
import { Rectangle } from '../../geometry/rectangle'

export class CorridorSplit implements Partitioner {
  public constructor(private readonly corridorWidth: number, private readonly maxRoomWidth: number) {}

  public weight = 20

  public canPartition(shape: Shape, exits: Vector[]): boolean {
    const exit = exits[0]
    const bounds = shape.bounds().grow()
    const axis: Axis = bounds.top === exit.y || bounds.bottom === exit.y ? 'x' : 'y'
    const minimalSideLength = Math.min(bounds.width, bounds.height)
    const minimalSplitSidelength =
      axis === 'x' ? Math.min(exit.x - bounds.left, bounds.right - exit.x) : Math.min(exit.y - bounds.top, bounds.bottom - exit.y)
    const axisIsSplittable = minimalSplitSidelength > this.maxRoomWidth + Math.ceil(this.corridorWidth / 2)
    return axisIsSplittable && minimalSideLength > this.maxRoomWidth
  }

  public partition(context: SpacePartitioner, random: Random, shape: Shape, exits: Vector[]): Partition {
    const [head, ...tail] = exits
    const bounds = shape.bounds()
    const axis: Axis = bounds.grow().top === head.y || bounds.grow().bottom === head.y ? 'x' : 'y'
    const split = axis === 'x' ? head.x - bounds.left : head.y - bounds.top
    const [left, middle, right] = this.split(bounds, axis, split - Math.ceil(this.corridorWidth / 2), this.corridorWidth)

    const yOffset = Vector.fromDirection('down').mult(4)
    const xOffset = Vector.fromDirection('right').mult(4)
    const leftExit =
      axis === 'x'
        ? random.betweenVectors(left.grow().topRight.add(yOffset), left.grow().bottomRight.minus(yOffset))
        : random.betweenVectors(left.grow().bottomLeft.add(xOffset), left.grow().bottomRight.minus(xOffset))
    const rightExit =
      axis === 'x'
        ? random.betweenVectors(right.grow().topLeft.add(yOffset), right.grow().bottomLeft.minus(yOffset))
        : random.betweenVectors(right.grow().topLeft.add(xOffset), right.grow().topRight.minus(xOffset))
    const leftTail = tail.filter(e => context.exitConnectsShape(left, e))
    const rightTail = tail.filter(e => context.exitConnectsShape(right, e))
    return {
      shape,
      type: 'node',
      exit: 1,
      partitions: [
        context.partition(left, [leftExit, ...leftTail]),
        context.createCorridor(middle, exits),
        context.partition(right, [rightExit, ...rightTail]),
      ],
      connections: [
        { from: 0, to: 1 },
        { from: 1, to: 2 },
      ],
    }
  }

  private split(r: Rectangle, axis: Axis, split: number, width: number): [Rectangle, Rectangle, Rectangle] {
    let a: Rectangle
    let b: Rectangle
    let c: Rectangle
    if (axis === 'x') {
      a = Rectangle.fromBounds(r.left, r.left + split - 1, r.top, r.bottom)
      b = Rectangle.fromBounds(a.right + 2, a.right + 1 + width, r.top, r.bottom)
      c = Rectangle.fromBounds(b.right + 2, r.right, r.top, r.bottom)
    } else {
      a = Rectangle.fromBounds(r.left, r.right, r.top, r.top + split - 1)
      b = Rectangle.fromBounds(r.left, r.right, a.bottom + 2, a.bottom + 1 + width)
      c = Rectangle.fromBounds(r.left, r.right, b.bottom + 2, r.bottom)
    }
    return [a, b, c]
  }
}
