import { ComponentName, TlbSystem, TlbWorld } from '../tlb'
import { Entity } from '../ecs/entity'
import { TakeTurnComponent } from '../components/rounds'
import { clearExpiredEffects, updateEffects } from '../component-reducers/apply-effect'
import { Random } from '../random'
import { Distribution } from '../random/distributions'
import { InventoryComponent } from '../components/items'

export class StartRound implements TlbSystem {
  public readonly components: ComponentName[] = ['start-turn']

  private readonly uniform: Random
  public constructor(rng: Distribution) {
    this.uniform = new Random(rng)
  }

  public update(world: TlbWorld, entity: Entity): void {
    clearExpiredEffects(world, entity)
    updateEffects(world, entity, this.uniform)
    world.getComponent<InventoryComponent>(entity, 'inventory')!.content.forEach(item => {
      clearExpiredEffects(world, item)
      updateEffects(world, item, this.uniform)
    })

    world
      .editEntity(entity)
      .removeComponent('start-turn')
      .withComponent<TakeTurnComponent>('take-turn', {
        moved: false,
        acted: false,
        selectionState: undefined,
      })
  }
}
