import { TlbWorld } from '../tlb'
import { Attack } from '../components/action'
import { Entity } from '../ecs/entity'
import { Random } from '../random'
import { Log, LogResource } from '../resources/log'
import { calculateHitChance, HitChanceCalculation } from './calculate-hit-chance'
import { applyEffect, EffectResult, clearExpiredEffects } from './apply-effect'

export interface AttackResult {
  isHit: boolean
  isCritical: boolean
  chance: HitChanceCalculation
  effects: Partial<EffectResult>[]
}

export function attackTarget(world: TlbWorld, random: Random, entity: Entity, target: Entity, attack: Attack): AttackResult {
  const chance = calculateHitChance(world, entity, target, attack)

  const isHit = random.decision(chance.final)
  const isCritical = isHit && random.decision(chance.criticalHit)
  let effects: Partial<EffectResult>[] = []
  if (isHit) {
    effects = attack.effects.map(effect => applyEffect(world, random, target, { source: entity, effect, isCritical, isPiercing: false }))
    clearExpiredEffects(world, target)
  }
  const result: AttackResult = { isHit, isCritical, chance, effects }
  const log: Log = world.getResource<LogResource>('log')
  log.attack(world, entity, target, result)
  return result
}
