import { Action, action, Movement, ChangeEquipment, Trigger, Status, Jump, Attack, SubAction } from '../components/action'
import {
  Effect,
  damage,
  stun,
  kill,
  heal,
  negateEffects,
  defend,
  immobilize,
  critMultiplier,
  aim,
  critChance,
  penetrate,
  cooldown,
} from '../components/effects'
import { copy } from '../object-utils'
import { AttackResult } from '../component-reducers/attack-target'
import { ModAction } from '../components/items'

function movement(range: number): Movement {
  return { kind: 'movement', range, target: 'self' }
}

function jump(range: number): Jump {
  return { kind: 'jump', range, target: 'self' }
}

function changeEquipment(): ChangeEquipment {
  return { kind: 'changeEquipment' }
}

function trigger(): Trigger {
  return { kind: 'trigger' }
}

function attack(range: number, accuracy: number, effects: Effect[], grants?: (hit: AttackResult) => SubAction[]): Attack {
  return { kind: 'attack', range, accuracy, effects, target: 'enemy', grants }
}

function status(effects: Effect[], target: 'self' | 'item' = 'self'): Status {
  return { kind: 'status', effects, target }
}

function modifySubActions(
  actions: SubAction[],
  attackModifier: (a: Attack) => void,
  movementModifier: (m: Movement) => void,
  effectModifier: (e: Effect, onself: boolean) => void
): SubAction[] {
  return actions.map(a => {
    switch (a.kind) {
      case 'attack': {
        const effects = a.effects.map(e => copy(e))
        effects.forEach(e => effectModifier(e, false))
        const newAttack = attack(a.range, a.accuracy, effects)
        attackModifier(newAttack)
        return newAttack
      }
      case 'status': {
        const effects = a.effects.map(e => copy(e))
        effects.forEach(e => effectModifier(e, true))
        return status(effects)
      }
      case 'movement': {
        const movement = copy(a)
        movementModifier(movement)
        return movement
      }
      case 'trigger':
      case 'changeEquipment':
      case 'jump':
        return copy(a)
    }
  })
}

export function addModification(type: ModAction, base: Action): Action {
  switch (type) {
    case 'overcharge':
      return action(
        'overcharge',
        base.cost,
        modifySubActions(
          [...base.subActions, status([cooldown(2)], 'item')],
          a => {
            a.accuracy = Math.max(2, a.accuracy - 2)
            a.range = a.range + 2
            a.grants = undefined
          },
          () => null,
          e => {
            e.value = e.value !== undefined ? e.value + 2 : undefined
          }
        ),
        base.characterTypes
      )
    case 'acrobatics': {
      const actions = copy(base.subActions)
      const firstAttack = actions.find(a => a.kind === 'attack') as Attack
      firstAttack.grants = result => {
        if (result.isCritical) {
          return [jump(6), attack(1, firstAttack.accuracy, firstAttack.effects, firstAttack.grants)]
        } else {
          return []
        }
      }
      const chain = action('chain', 'both', [jump(6), ...actions], ['yellow_player', 'boss'])
      return chain
    }
    case 'frenzy': {
      const first = copy(base.subActions.find(a => a.kind === 'attack') as Attack)
      const melee = first.range === 1
      const frenzy = action('frenzy', 'both', [first], ['yellow_player', 'red_player', 'boss'])
      first.grants = result => {
        if (result.isHit) {
          const lastAttack = frenzy.subActions[frenzy.subActions.length - 1] as Attack
          const accuracy = Math.max(2, lastAttack.accuracy - 2)
          if (melee) {
            return [
              status([critChance(-1, 0), critMultiplier(0.5, 0)]),
              movement(5),
              attack(1, accuracy, lastAttack.effects, lastAttack.grants),
            ]
          } else {
            return [status([aim(-1, 0)]), attack(lastAttack.range, accuracy, lastAttack.effects, lastAttack.grants)]
          }
        } else {
          return [status([immobilize(1)])]
        }
      }
      return frenzy
    }
    case 'doubleShot':
      return action('double shot', base.cost, [...base.subActions, ...base.subActions, status([cooldown(2)], 'item')], base.characterTypes)
    case 'headshot':
      return action(
        'headshot',
        base.cost,
        modifySubActions(
          [status([critChance(2, 1), critMultiplier(2, 1)], 'self'), ...base.subActions, status([cooldown(1)], 'item')],
          () => null,
          () => null,
          () => null
        ),
        base.characterTypes
      )
    case 'execute':
      return action(
        'execute',
        base.cost,
        modifySubActions(
          [status([critChance(2, 1), critMultiplier(2, 1)]), ...base.subActions, status([cooldown(1)], 'item')],
          a => {
            a.effects.push(stun(1))
            a.range = Math.min(1, Math.floor(a.range / 2))
            a.grants = undefined
          },
          () => null,
          () => null
        ),
        base.characterTypes
      )
    case 'bloody':
      return action(
        base.name,
        base.cost,
        modifySubActions(
          base.subActions,
          a => {
            a.grants = result => (result.effects.some(e => e.killed) ? [status([critChance(2, 2), critMultiplier(1.1, 2)])] : [])
          },
          () => null,
          () => null
        ),
        base.characterTypes
      )
    case 'fast':
      return action(base.name, 'either', base.subActions, base.characterTypes)
    case 'mobile':
      return action('run and gun', 'action', [...base.subActions, movement(6)], base.characterTypes)
    case 'penetrating':
      return action(
        base.name,
        base.cost,
        modifySubActions(
          base.subActions,
          a => {
            a.effects.push(penetrate(2, 0))
          },
          () => null,
          () => null
        ),
        base.characterTypes
      )
    case 'stabilizing':
      return action(
        base.name,
        base.cost,
        modifySubActions(
          base.subActions,
          a => {
            a.grants = result => (result.isHit ? [status([aim(2, 2)])] : [])
          },
          () => null,
          () => null
        ),
        base.characterTypes
      )
    case 'stunning':
      return action(
        base.name,
        base.cost,
        modifySubActions(
          base.subActions,
          a => {
            a.effects.push(stun(1))
          },
          () => null,
          () => null
        ),
        base.characterTypes
      )
    case 'ranged':
      return action(
        base.name,
        base.cost,
        modifySubActions(
          base.subActions,
          a => {
            a.range += 1
          },
          () => null,
          () => null
        ),
        base.characterTypes
      )
    case 'enhanced':
      return action(
        base.name,
        base.cost,
        modifySubActions(
          base.subActions,
          () => null,
          () => null,
          e => {
            if (e.type === 'damage') {
              e.value = Math.floor(e.value! * 1.3)
            }
          }
        ),
        base.characterTypes
      )
  }
}

export type ActionType =
  | 'endTurn'
  | 'move'
  | 'heal'
  | 'kill'
  | 'trigger'
  | 'hit'
  | 'kick'
  | 'rush'
  | 'shoot'
  | 'tighten'
  | 'bodyShield'
  | 'noMove'
  | 'changeEquipment'
export function createAction(type: ActionType): Action {
  switch (type) {
    case 'kick':
      return action('kick', 'either', [status([critChance(4, 0)]), attack(1, 8, [damage(4)])])
    case 'trigger':
      return action('trigger', 'either', [trigger()])
    case 'noMove':
      return action('no move', 'movement', [])
    case 'changeEquipment':
      return action('change equipment', 'either', [changeEquipment()])
    case 'endTurn':
      return action('end turn', 'both', [])
    case 'bodyShield':
      return action('body shield', 'movement', [status([defend(5, 1)])])
    case 'move':
      return action('move', 'movement', [movement(4)])
    case 'heal':
      return action('heal', 'either', [status([heal(), negateEffects()])])
    case 'kill':
      return action('kill', 'action', [status([kill()])])
    case 'hit':
      return action('hit', 'action', [attack(1, 8, [damage(3)])])
    case 'rush':
      return action('rush', 'all', [movement(6)])
    case 'shoot':
      return action('shoot', 'action', [attack(10, 6, [damage(5)])])
    case 'tighten':
      return action('tighten', 'action', [status([defend(4, 1)])])
  }
}
