export function getRandomMod(random: Random, kind: ModKind, item: Item, region: RegionsType): Mod | undefined {
  const availableMods = mods.filter(mod => {
    const hasKind = mod.kind === kind
    const regionMatches = mod.occurs.some(o => region === o)
    const itemMatches = mod.matches.some(
      m => m.kind === item.kind && (m.tags.length === 0 || m.tags.some(t => item.tags.some(tag => t === tag)))
    )
    return hasKind && regionMatches && itemMatches
  })
  return random.pick(availableMods)
}
