import { Shape } from '../geometry/shape'
import { Distribution } from '../random/distributions'
import { Vector } from '../spatial'
import { Random } from '../random'

import { Partitioner, Partition } from './partitioners/Partitioner'
import * as partitioners from './partitioners'

export class SpacePartitioner {
  private random: Random
  private partitioners: Partitioner[]

  public constructor(rng: Distribution) {
    this.random = new Random(rng)
    this.partitioners = [new partitioners.NoSplit(10)]
  }

  public registerPartitioner(partitioner: Partitioner): void {
    this.partitioners.push(partitioner)
  }

  public partition(shape: Shape, exits: Vector[]): Partition {
    const partitioners = this.partitioners.filter(p => p.canPartition(shape, exits) && p.weight > 0)
    let partitioner: Partitioner = this.partitioners[0]
    if (partitioners.length > 0) {
      const decision = this.random.weightedDecision(partitioners.map(p => p.weight))
      partitioner = partitioners[decision]
    }
    return partitioner.partition(this, this.random, shape, exits)
  }

  public createRoom(shape: Shape, exits: Vector[]): Partition {
    return { type: 'final', shape, exits, kind: 'room' }
  }
  public createCorridor(shape: Shape, exits: Vector[]): Partition {
    return { type: 'final', shape, exits, kind: 'corridor' }
  }
  public exitConnectsShape(shape: Shape, exit: Vector): boolean {
    return shape
      .bounds()
      .grow()
      .containsVector(exit)
  }
}
