import { TlbWorld } from '../tlb'
import { Entity } from '../ecs/entity'
import { InventoryComponent, ItemComponent } from '../components/items'
import { ItemBaseType } from '../assets/items'

export function hasItem(world: TlbWorld, entity: Entity, type: ItemBaseType): boolean {
  const inventory = world.getComponent<InventoryComponent>(entity, 'inventory')!.content
  return inventory.some(i => world.getComponent<ItemComponent>(i, 'item')!.item.base === type)
}
