import { createAction } from './actions'
import { Item, WearableKind, ItemTag } from '../components/items'
import { Effect, defend, cooldownBuff } from '../components/effects'
import { Random } from '../random'
import { Action } from '../components/action'
import { RegionsType } from '../components/region'

export type ItemBaseType =
  | 'deathPill'
  | 'bandages'
  | 'nailGun'
  | 'gun'
  | 'powerGauntlet'
  | 'rifle'
  | 'sniperRifle'
  | 'leatherJacket'
  | 'idCard'
  | 'boots'

function weapon(
  base: ItemBaseType,
  name: string,
  tags: ItemTag[],
  description: string,
  weight: number,
  action: Action,
  attachments: number,
  effects: Effect[] = []
): Item {
  return { base, tags, kind: 'weapon', name, shortName: name, description, actions: [action], weight, attachments, effects }
}

function consumable(base: ItemBaseType, name: string, tags: ItemTag[], description: string, weight: number, action: Action): Item {
  return { base, tags, kind: 'consumable', attachments: 0, actions: [action], name, shortName: name, description, weight, effects: [] }
}

function scrap(base: ItemBaseType, name: string, tags: ItemTag[], description: string): Item {
  return { base, tags, kind: 'scrap', attachments: 0, name, shortName: name, description, weight: 0, effects: [], actions: [] }
}

function equipment(
  base: ItemBaseType,
  kind: WearableKind,
  name: string,
  tags: ItemTag[],
  description: string,
  weight: number,
  action: Action,
  effects: Effect[],
  attachments: number
): Item {
  return { base, kind, tags, name, shortName: name, description, actions: [action], effects, weight, attachments }
}

export function createItem(base: ItemBaseType, _random: Random, _region: RegionsType): Item {
  switch (base) {
    case 'deathPill':
      return consumable(base, 'death pill', [], 'a pill that kills you', 1, createAction('kill'))
    case 'bandages':
      return consumable(base, 'bandages', [], 'a bandage to stop bleeding', 1, createAction('heal'))
    case 'gun': {
      return weapon(base, 'gun', ['gun'], 'a gun.', 3, createAction('shoot'), 1)
    }
    case 'nailGun': {
      return weapon(
        base,
        'nail gun',
        ['high-tech'],
        'a tool to drive nails into wood, control panels or bodies.',
        5,
        createAction('shoot'),
        1
      )
    }
    case 'rifle':
      return weapon(base, 'rifle', ['rifle'], 'a regular rifle.', 5, createAction('shoot'), 2)
    case 'sniperRifle': {
      return weapon(base, 'sniper rifle', ['rifle'], '', 6, createAction('shoot'), 2)
    }
    case 'powerGauntlet': {
      return weapon(base, 'power gauntlet', ['melee'], 'a powerful gauntlet, worn at one hand.', 5, createAction('hit'), 1, [
        cooldownBuff(1, undefined),
      ])
    }
    case 'boots': {
      return equipment(base, 'boots', 'boots', [], 'some boots', 2, createAction('kick'), [cooldownBuff(1, undefined)], 2)
    }
    case 'leatherJacket':
      return equipment(base, 'armor', 'leather jacket', [], 'a nice leather jacket', 3, createAction('tighten'), [defend(5, undefined)], 1)
    case 'idCard':
      return scrap(base, 'id card', [], 'id card')
  }
}
