import { TlbWorld } from '../tlb'
import { Entity } from '../ecs/entity'
import { Action } from '../components/action'
import { EquipedItemsComponent, InventoryComponent } from '../components/items'
import { SelectableAction } from '../ui/tabs/action-selector'
import { instantiateItem } from './instantiate-item'
import { getCanAct, getCanMove, getCooldownBuff } from './stats'
import { ActionType, createAction } from '../assets/actions'
import { PositionComponent } from '../components/position'
import { FunctionalShape } from '../geometry/functional-shape'
import { findTriggers } from './trigger'

export function calculateAvailableActions(world: TlbWorld, entity: Entity, withConsumables: boolean): SelectableAction[] {
  const canAct = getCanAct(world, entity)
  const canMove = getCanMove(world, entity)
  const cooldownBuff = getCooldownBuff(world, entity)
  const playerActions = buildPlayerSelectableActions(entity, canMove, canAct)
  const triggerActions = buildTriggerActions(world, entity)
  const equipmentSelectableActions = buildEquipmentSelectableActions(world, entity, canMove, canAct, cooldownBuff)
  let consumableActions: SelectableAction[] = []
  if (withConsumables) {
    consumableActions = buildConsumableSelectableActions(world, entity, canMove, canAct)
  }
  return playerActions
    .concat(equipmentSelectableActions)
    .concat(consumableActions)
    .concat(triggerActions)
}

function buildTriggerActions(world: TlbWorld, entity: Entity): SelectableAction[] {
  const position = world.getComponent<PositionComponent>(entity, 'position')!
  const neighbourhood = FunctionalShape.lN(position.position, 1, true)
  const triggers = findTriggers(world, position.level, neighbourhood)
  return triggers
    .filter(t => t.component.inTurnBased)
    .map(t => ({
      owner: t.component.name,
      action: createAction('trigger'),
      entity: t.entity,
    }))
}

function buildPlayerSelectableActions(entity: Entity, canMove: boolean, canAct: boolean): SelectableAction[] {
  const defaultActions: ActionType[] = ['move', 'hit', 'rush', 'noMove', 'endTurn', 'changeEquipment']
  return defaultActions
    .map(a => createAction(a))
    .filter(action => actionAvailable(action, canMove, canAct))
    .map(action => ({
      action,
      entity,
    }))
}

function buildEquipmentSelectableActions(
  world: TlbWorld,
  entity: Entity,
  canMove: boolean,
  canAct: boolean,
  cooldownBuff: number
): SelectableAction[] {
  const equipment = world.getComponent<EquipedItemsComponent>(entity, 'equiped-items')!
  const actions: SelectableAction[] = []
  equipment.equipment.forEach(equiped => {
    const { item } = instantiateItem(world, equiped.entity)
    item.actions.forEach(action => {
      if (actionAvailable(action, canMove, canAct)) {
        if (action.cooldown !== undefined) {
          action.cooldown -= cooldownBuff
        }
        actions.push({ action, owner: item.shortName, entity: equiped.entity })
      }
    })
  })
  return actions
}

function buildConsumableSelectableActions(world: TlbWorld, entity: Entity, canMove: boolean, canAct: boolean): SelectableAction[] {
  const inventory = world.getComponent<InventoryComponent>(entity, 'inventory')!
  return inventory.content
    .map(inventoryContent => {
      const { item } = instantiateItem(world, inventoryContent)
      if (item.kind === 'consumable' && item.actions.length > 0 && actionAvailable(item.actions[0], canMove, canAct)) {
        return { action: item.actions[0], owner: item.shortName, entity: inventoryContent }
      }
      return undefined
    })
    .filter(a => a !== undefined) as SelectableAction[]
}

function actionAvailable(action: Action, canMove: boolean, canAct: boolean): boolean {
  switch (action.cost) {
    case 'all':
      return canAct && canMove
    case 'action':
      return canAct && !canMove
    case 'movement':
      return canMove
    default:
      return true
  }
}
