import { BodyPart, CharacterStats, CharacterStatsComponent, createCharacterStatsComponent } from '../components/character-stats'
import { TlbWorld } from '../tlb'
import { features, FeatureType } from './features'
import { FeatureComponent } from '../components/feature'
import { FovComponent } from '../components/fov'
import { InventoryComponent, EquipedItemsComponent, createEntityWithItemComponent, ItemComponent, Item } from '../components/items'
import { ActiveEffectsComponent } from '../components/effects'
import { Entity } from '../ecs/entity'
import { AiComponent } from '../components/ai'
import { addDialog } from './dialogs'
import { TriggersComponent } from '../components/trigger'
import { SetSpace } from '../spatial/set-space'
import { WorldMapResource } from '../resources/world-map'
import { CharacterTypeComponent } from '../components/character-type'
import { Random } from '../random'
import { RegionsType } from '../components/region'
import { dropAt } from '../array-utils'
import { instantiateItem } from '../component-reducers/instantiate-item'

function humanoidBodyParts(): { [key: string]: BodyPart } {
  return {
    head: { type: 'head', itemAttachments: ['helmet'] },
    leftArm: { type: 'arm', itemAttachments: ['glove', 'weapon'] },
    rightArm: { type: 'arm', itemAttachments: ['glove', 'weapon'] },
    torso: { type: 'torso', itemAttachments: ['armor'] },
    leftLeg: { type: 'leg', itemAttachments: ['pants', 'boots'] },
    rightLeg: { type: 'leg', itemAttachments: ['pants', 'boots'] },
  }
}

const charactersStatsDefinition = {
  player: {
    bodyParts: humanoidBodyParts(),
    health: 20,
    strength: 5,
    aim: 8,
  },
  guard: {
    bodyParts: humanoidBodyParts(),
    health: 5,
    strength: 5,
    aim: 6,
  },
  civilian: {
    bodyParts: humanoidBodyParts(),
    health: 5,
    strength: 4,
    aim: 3,
  },
  eliteGuard: {
    bodyParts: humanoidBodyParts(),
    health: 11,
    strength: 8,
    aim: 6,
  },
  boss: {
    bodyParts: humanoidBodyParts(),
    health: 18,
    strength: 10,
    aim: 8,
  },
}
export type CharacterStatsType = keyof typeof charactersStatsDefinition
export const characterStats: { [key in CharacterStatsType]: CharacterStats } = charactersStatsDefinition

export interface CharacterCreatorOptions {
  type: PlayerType
}
export interface CharacterCreator {
  (world: TlbWorld, random: Random, region: RegionsType, options: Partial<CharacterCreatorOptions>): Entity
}
const characterCreatorsDefinition: { [key in CharacterStatsType]: CharacterCreator } = {
  boss: (world: TlbWorld, random: Random, region: RegionsType) => createBoss(world, random, region),
  player: (world: TlbWorld, random: Random, region: RegionsType, options: Partial<CharacterCreatorOptions>) =>
    createPlayer(world, random, options.type || 'yellow_player', region),
  eliteGuard: (world: TlbWorld, random: Random, region: RegionsType) =>
    createGuard(world, random, 'some elite guard', 'eliteGuard', 'eliteGuard', region),
  guard: (world: TlbWorld, random: Random, region: RegionsType) => createGuard(world, random, 'some guard', 'guard', 'guard', region),
  civilian: (world: TlbWorld, random: Random) => createCivilian(world, random, 'some guy', 'civilian', 'civilian'),
}

export function createBoss(world: TlbWorld, random: Random, region: RegionsType): Entity {
  const boss = createEmptyNpc(world, 'overseer', 'boss', 'eliteGuard')
  addDialog(world, boss, 'guardRandomRemarks')
  const rifle = createEntityWithItemComponent(world, 'rifle', random, region)
  const nailGun = createEntityWithItemComponent(world, 'nailGun', random, region)
  const leatherJacket = createEntityWithItemComponent(world, 'leatherJacket', random, region)
  take(world, boss, rifle)

  take(world, boss, nailGun)
  take(world, boss, leatherJacket)
  equip(world, boss, nailGun, ['leftArm'])
  equip(world, boss, leatherJacket, ['torso', 'leftArm', 'rightArm'])
  return boss
}

export function createPlayer(world: TlbWorld, random: Random, type: PlayerType, region: RegionsType): Entity {
  const player = createCharacter(world, 'player', type)
  const width = world.getResource<WorldMapResource>('map').width

  world
    .editEntity(player)
    .withComponent<{}>('player', {})
    .withComponent<FovComponent>('fov', { fov: new SetSpace(width) })
    .withComponent<{}>('viewport-focus', {})

  setType(world, player, type)

  const idCard = createEntityWithItemComponent(world, 'idCard', random, region)
  const rifle = createEntityWithItemComponent(world, 'rifle', random, region)
  const sniperRifle = createEntityWithItemComponent(world, 'sniperRifle', random, region)
  const gun = createEntityWithItemComponent(world, 'gun', random, region)
  const leatherJacket = createEntityWithItemComponent(world, 'leatherJacket', random, region)
  const boots = createEntityWithItemComponent(world, 'boots', random, region)
  take(world, player, idCard)
  take(world, player, rifle)
  take(world, player, sniperRifle)
  take(world, player, gun)
  take(world, player, leatherJacket)
  take(world, player, boots)
  equip(world, player, gun, ['leftArm'])
  equip(world, player, leatherJacket, ['torso'])
  equip(world, player, boots, ['leftLeg', 'rightLeg'])
  return player
}

export function setType(world: TlbWorld, entity: Entity, type: CharacterType): void {
  world.editEntity(entity).withComponent<CharacterTypeComponent>('character-type', { type })
}

export function createCivilian(
  world: TlbWorld,
  _random: Random,
  name: string,
  statsType: CharacterStatsType,
  featureType: FeatureType
): Entity {
  const npc = createEmptyNpc(world, name, statsType, featureType)
  setType(world, npc, 'civilian')
  addDialog(world, npc, 'civilianDialog')
  return npc
}

export function createGuard(
  world: TlbWorld,
  random: Random,
  name: string,
  statsType: CharacterStatsType,
  featureType: FeatureType,
  region: RegionsType
): Entity {
  const npc = createEmptyNpc(world, name, statsType, featureType)
  world.editEntity(npc).withComponent<AiComponent>('ai', { type: 'rushing', state: 'idle', interest: undefined, distrust: 0 })
  const rifle = createEntityWithItemComponent(world, 'rifle', random, region)
  take(world, npc, rifle)
  equip(world, npc, rifle, ['leftArm'])
  setType(world, npc, 'henchman')
  addDialog(world, npc, 'guardRandomRemarks')
  return npc
}

export function take(world: TlbWorld, character: Entity, entity: Entity): void {
  world.getComponent<InventoryComponent>(character, 'inventory')!.content.push(entity)
  const activeEffects = world.getComponent<ActiveEffectsComponent>(character, 'active-effects')!
  activeEffects.effects = activeEffects.effects.filter(e => e.source !== entity)
}

export function replacingEquip(world: TlbWorld, character: Entity, entity: Entity, item: Item): void {
  const equipped = world.getComponent<EquipedItemsComponent>(character, 'equiped-items')!
  const stats = world.getComponent<CharacterStatsComponent>(character, 'character-stats')!
  const possibleBodyParts: string[] = []
  Object.keys(stats.current.bodyParts).forEach(key => {
    const bodyPart = stats.current.bodyParts[key]
    bodyPart.itemAttachments.filter(kind => kind === item.kind).forEach(() => possibleBodyParts.push(key))
  })

  equipped.equipment.forEach(e => {
    const equipedItem = world.getComponent<ItemComponent>(e.entity, 'item')!.item
    if (equipedItem.kind === item.kind) {
      const blocking = e.bodyParts.some(part => possibleBodyParts.findIndex(p => p === part) >= 0)
      if (blocking) {
        unequip(world, character, e.entity)
      }
    }
  })

  const availableBodyParts = new Set(possibleBodyParts)
  if (availableBodyParts.size >= item.attachments) {
    const bodyParts: string[] = []
    availableBodyParts.forEach(part => {
      if (bodyParts.length < item.attachments) {
        bodyParts.push(part)
      }
    })
    equip(world, character, entity, bodyParts)
  }
}

export function equip(world: TlbWorld, character: Entity, entity: Entity, bodyParts: string[]): void {
  const { item } = instantiateItem(world, entity)
  world.getComponent<EquipedItemsComponent>(character, 'equiped-items')!.equipment.push({ entity, bodyParts })
  const activeEffects = world.getComponent<ActiveEffectsComponent>(character, 'active-effects')!
  item.effects.forEach(e => activeEffects.effects.push({ source: entity, effect: e, isCritical: false, isPiercing: false }))
}

export function unequip(world: TlbWorld, character: Entity, entity: Entity): void {
  const equipped = world.getComponent<EquipedItemsComponent>(character, 'equiped-items')!
  const attachementIndex = equipped.equipment.findIndex(e => e.entity === entity)
  dropAt(equipped.equipment, attachementIndex)
}

export function createEmptyNpc(world: TlbWorld, name: string, statsType: CharacterStatsType, featureType: FeatureType): Entity {
  const character = createCharacter(world, statsType, featureType)
  return world
    .editEntity(character)
    .withComponent('npc', {})
    .withComponent<TriggersComponent>('triggers', { entities: [], type: 'dialog', name, inTurnBased: false }).entity
}

export function createCharacter(world: TlbWorld, statsType: CharacterStatsType, featureType: FeatureType): Entity {
  return world
    .createEntity()
    .withComponent<InventoryComponent>('inventory', { content: [] })
    .withComponent<EquipedItemsComponent>('equiped-items', { equipment: [] })
    .withComponent<FeatureComponent>('feature', { feature: () => features[featureType] })
    .withComponent<CharacterStatsComponent>('character-stats', createCharacterStatsComponent(statsType))
    .withComponent<ActiveEffectsComponent>('active-effects', { effects: [] }).entity
}

export type CharacterCreatorType = keyof typeof characterCreatorsDefinition
export const characterCreators: { [key in CharacterCreatorType]: CharacterCreator } = characterCreatorsDefinition

export type PlayerType = 'red_player' | 'green_player' | 'blue_player' | 'yellow_player'
export type CharacterType = PlayerType | 'civilian' | 'henchman' | 'boss'
