import { Renderer } from '../../renderer/renderer'
import { Vector } from '../../spatial'
import { Entity } from '../../ecs/entity'
import { TlbWorld } from '../../tlb'
import { primary, gray } from '../../renderer/palettes'
import { KeyboardCommand } from '../../resources/input'
import { Rectangle } from '../../geometry/rectangle'
import { createInventoryDescription, InventoryDescription, ItemWithEntity } from '../../component-reducers/inventory-description'
import { ItemSelector } from '../selector'
import { consumeItem } from '../../component-reducers/consume-item'

import { Tab, TabView, TabsKey } from '../tabs'
import { unequip, replacingEquip } from '../../assets/characters'
import { applyEffect } from '../../component-reducers/apply-effect'
import { Random } from '../../random'
import { TakeTurnComponent } from '../../components/rounds'

export interface State {
  focus: Entity
  inventory?: InventoryDescription
  takeTurn?: TakeTurnComponent
}

export class FullInventoryView implements TabView {
  private readonly state: State

  private readonly items: Rectangle
  private readonly descriptions: Rectangle

  public readonly itemSelector: ItemSelector<ItemWithEntity>

  public constructor(public readonly content: Rectangle, public readonly uniform: Random, focus: Entity) {
    this.state = { focus }

    const width = Math.floor(content.width / 2)
    this.items = new Rectangle(content.left, content.top, width, content.height)
    this.descriptions = new Rectangle(this.items.right, content.top, content.width - width, content.height)

    this.itemSelector = new ItemSelector([])
  }

  public render(renderer: Renderer): void {
    if (this.state.inventory !== undefined) {
      const inventory = this.state.inventory

      this.renderInventory(renderer, inventory, this.itemSelector.hoveredIndex)
      if (this.itemSelector.hoveredIndex) {
        this.renderDescription(renderer, inventory, this.itemSelector.hoveredIndex)
      }
    }
  }

  public renderInventory(renderer: Renderer, inventory: InventoryDescription, hovered: number | undefined) {
    const inventoryItems = inventory.items || []
    let y = 0

    inventoryItems.forEach(item => {
      const attachement = inventory.equipment.find(e => e.entity === item.entity)
      let attached = ''
      if (attachement) {
        attached = '*'
      }

      renderer.text(
        `${y + 1} ${item.item.name}${attached}`,
        this.items.topLeft.add(new Vector([0, y])),
        primary[1],
        hovered === y ? gray[1] : undefined
      )
      y++
    })

    if (inventory.maximumWeight !== undefined) {
      renderer.text(`weight: ${inventory.currentWeight}/${inventory.maximumWeight}`, this.content.bottomLeft, primary[1])
    }
  }

  public renderDescription(renderer: Renderer, inventory: InventoryDescription, index: number) {
    const inventoryItems = inventory.items || []
    const inventoryItem = inventoryItems[index]
    let y = 0
    if (inventoryItem !== undefined) {
      const item = inventoryItem.item
      if (item.actions.length > 0) {
        y += renderer.flowText(
          `${item.description}`,
          this.descriptions.topLeft.add(new Vector([0, y])),
          this.descriptions.width,
          primary[1]
        )
        y += renderer.flowText(
          'actions: ' + item.actions.map(a => a.name).join(),
          this.descriptions.topLeft.add(new Vector([0, y])),
          this.descriptions.width,
          primary[1]
        )
      }

      y += renderer.flowText(
        'mods: ' + inventoryItem.mods.map(a => a.adjective).join(),
        this.descriptions.topLeft.add(new Vector([0, y])),
        this.descriptions.width,
        primary[1]
      )

      if (this.state.takeTurn === undefined) {
        let action: string | undefined = undefined
        if (item.kind === 'consumable') {
          action = 'consume'
        } else if (item.attachments > 0) {
          const equipped = inventory.equipment.some(i => i.entity === inventoryItem.entity)
          action = equipped ? 'unequip' : 'equip'
        }
        if (action) {
          renderer.text(action, this.descriptions.topLeft.add(new Vector([0, y])), primary[1])
          y++
        }
      }
    }
  }

  public update(world: TlbWorld): void {
    this.state.inventory = createInventoryDescription(world, this.state.focus)
    this.state.takeTurn = world.getComponent(this.state.focus, 'take-turn')
    this.itemSelector.setItems(this.state.inventory.items)
    this.itemSelector.update(world, this.content)

    if (this.itemSelector.selectedIndex !== undefined && this.state.takeTurn === undefined) {
      this.use(world, this.itemSelector.selectedIndex)
    }
  }

  private use(world: TlbWorld, index: number) {
    const inventory = this.state.inventory!
    const item = inventory.inventory.content[index]
    const inventoryItems = inventory.items || []
    const selectedItem = inventoryItems[index].item
    if (selectedItem.kind === 'consumable' && selectedItem.actions.length > 0) {
      selectedItem.actions[0].subActions.forEach(action => {
        if (action.kind === 'status') {
          action.effects.forEach(effect => {
            applyEffect(world, this.uniform, this.state.focus, {
              source: item,
              effect,
              isCritical: false,
              isPiercing: false,
            })
          })
        }
      })
      consumeItem(world, this.state.focus, item)
    } else if (selectedItem.attachments > 0) {
      const attachementIndex = inventory.equipment.findIndex(e => e.entity === item)

      if (attachementIndex >= 0) {
        unequip(world, this.state.focus, item)
      } else {
        replacingEquip(world, this.state.focus, item, selectedItem)
      }
    }
  }
}

export class MinimizedInventoryView implements TabView {
  private readonly state: State
  private readonly inventoryContent: Rectangle

  public constructor(public readonly content: Rectangle, focus: Entity) {
    const width = Math.floor(content.width / 2)
    this.inventoryContent = new Rectangle(content.left, content.top, width, content.height)
    this.state = { focus }
  }

  public render(renderer: Renderer): void {
    if (this.state.inventory !== undefined) {
      const inventory = this.state.inventory
      this.renderInventory(renderer, this.inventoryContent, inventory)
    }
  }

  public renderInventory(renderer: Renderer, content: Rectangle, inventory: InventoryDescription) {
    const inventoryItems = inventory.items || []

    let y = 0
    inventoryItems.forEach(item => {
      const attachement = inventory.equipment.find(e => e.entity === item.entity)
      let attached = ''
      if (attachement) {
        attached = '*'
      }
      renderer.text(`${item.item.name}${attached}`, content.topLeft.add(new Vector([0, y])), primary[1])
      y++
    })

    if (inventory.maximumWeight !== undefined) {
      renderer.text(
        `inventory (${inventory.currentWeight}/${inventory.maximumWeight})`,
        content.topLeft.add(new Vector([0, y])),
        primary[1]
      )
    }
  }

  public update(world: TlbWorld): void {
    this.state.inventory = createInventoryDescription(world, this.state.focus)
  }
}

export class Inventory implements Tab {
  public readonly key: TabsKey = 'inventory'
  public name: string = 'inventory'
  public readonly shortName: string = 'i'
  public readonly command: KeyboardCommand = 'inventory'

  public readonly minimizedHint: TabsKey = 'overview'

  public full: FullInventoryView | undefined
  public minimized: MinimizedInventoryView | undefined

  public constructor(public readonly focus: Entity, public readonly uniform: Random) {}

  public setFull(content: Rectangle): void {
    this.full = new FullInventoryView(content, this.uniform, this.focus)
  }

  public setMinimized(content: Rectangle): void {
    this.minimized = new MinimizedInventoryView(content, this.focus)
  }
}
