import { ActiveEffectsComponent, effect } from '../components/effects'
import { Entity } from '../ecs/entity'
import { TlbWorld } from '../tlb'
import { CharacterStatsComponent } from '../components/character-stats'
import { TakeTurnComponent } from '../components/rounds'

export function getAim(world: TlbWorld, entity: Entity): number {
  if (hasEffect(world, entity, 'confuse')) {
    return 0
  }
  const stats = world.getComponent<CharacterStatsComponent>(entity, 'character-stats')!
  return stats.current.aim + reduceActiveEffects(world, entity, 'aim')
}

export function getCriticalChance(world: TlbWorld, entity: Entity): number {
  return 3 + reduceActiveEffects(world, entity, 'crit-chance')
}

export function getCooldownBuff(world: TlbWorld, entity: Entity): number {
  return reduceActiveEffects(world, entity, 'cooldown-buff')
}

export function getCriticalMultiplier(world: TlbWorld, entity: Entity): number {
  return 2 + reduceActiveEffects(world, entity, 'crit-multiplier')
}

export function getDefense(world: TlbWorld, entity: Entity): number {
  return reduceActiveEffects(world, entity, 'defense')
}

export function getCanMove(world: TlbWorld, entity: Entity): boolean {
  if (hasEffect(world, entity, 'immobilize', 'stun')) {
    return false
  }
  return !world.getComponent<TakeTurnComponent>(entity, 'take-turn')!.moved
}

export function getCanAct(world: TlbWorld, entity: Entity): boolean {
  if (hasEffect(world, entity, 'stun')) {
    return false
  }
  return !world.getComponent<TakeTurnComponent>(entity, 'take-turn')!.acted
}

export function hasEffect(world: TlbWorld, entity: Entity, ...types: effect[]): boolean {
  const active = world.getComponent<ActiveEffectsComponent>(entity, 'active-effects')
  if (active !== undefined) {
    return active.effects.some(e => types.includes(e.effect.type))
  }
  return false
}

export function reduceActiveEffects(world: TlbWorld, entity: Entity, type: effect): number {
  const active = world.getComponent<ActiveEffectsComponent>(entity, 'active-effects')
  if (active !== undefined) {
    return active.effects
      .filter(e => e.effect.type === type)
      .map(e => e.effect.value || 0)
      .reduce((a, b) => a + b, 0)
  }
  return 0
}
